import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import CartItem from "./CartItem";

function MyCart(props) {
  const [selectedIds, setSelectedIds] = useState(null);
  const [all, setAll] = useState(false);
  const [total, setTotal] = useState(0);
  const cart = useSelector(state => state.cart);

  useEffect(() => {
    let tmp;
    if (cart && cart.products && cart.products.length) {
      if (selectedIds && selectedIds.length) {
        const curTotal = cart.products.reduce((prev, current) => {
          //
          if (selectedIds.indexOf(current.id) > -1) {
            tmp = total + current.discount * current.count;
            //
          } else {
            tmp = total;
          }
        }, 0);
        tmp = curTotal;
      }
    }
    setTotal(tmp);
  }, [cart]);

  function handleClickItem(id) {
    const ids = selectedIds || [];
    const index = ids.indexOf(id);
    if (index === -1) {
      ids.push(id);
    } else {
      ids.splice(index, 1);
    }
    if (equals(ids)) {
      setAll(true);
      setSelectedIds(ids);
    } else {
      setAll(false);
      setSelectedIds(ids);
    }
  }

  function handleClickSelectAll() {
    //return
    if (!cart || !cart.products || !cart.products.length) {
      return;
    }
    //ids
    if (equals(selectedIds)) {
      cancleAll();
    }
    //ids
    else {
      selectAll();
    }
  }

  //idscartproductid
  function equals(ids) {
    //number
    if (!ids) {
      return false;
    }
    const cartIds = cart.products.map(product => product.id).sort();
    const selectedIds = JSON.parse(JSON.stringify(ids)).sort();
    if (cartIds.length !== selectedIds.length) {
      return false;
    }
    for (let i = 0; i < cartIds.length; i++) {
      if (cartIds[i] !== selectedIds[i]) {
        return false;
      }
    }
    return true;
  }

  function selectAll() {
    setAll(true);
    setSelectedIds(cart.products.map(product => product.id));
  }

  function cancleAll() {
    setAll(false);
    setSelectedIds(null);
  }

  function handleOnPay() {
    if (!selectedIds || !selectedIds.length) {
      return;
    }
    props.handleChangeTab("payment", selectedIds, total);
  }

  return (
    <div className="my-cart">
      <div className="title"></div>
      <ul className="title-bar">
        <li className="label">
          <label
            onClick={handleClickSelectAll}
            className={all ? "selected" : ""}
          />
          <span onClick={handleClickSelectAll}></span>
        </li>
        <li className="info"></li>
        <li className="price"></li>
        <li className="count"></li>
        <li></li>
        <li className="action"></li>
      </ul>
      <ul className="goods">
        {cart && cart.products && cart.products.length ? (
          cart.products.map(product => (
            <CartItem
              product={product}
              key={product.id}
              {...props}
              selectedIds={selectedIds}
              handleClickItem={handleClickItem}
            />
          ))
        ) : (
          <li className="else">~~~</li>
        )}
      </ul>
      <ul className="action-bar">
        <li className="label">
          <label
            onClick={handleClickSelectAll}
            className={all ? "selected" : ""}
          />
          <span onClick={handleClickSelectAll}></span>
        </li>
        <li className="info" />
        <li className="count">
          
          <span className="number">
            {(selectedIds && selectedIds.length) || 0}
          </span>
          
        </li>
        <li className="total">
          <span className="number">{total && total.toFixed(2)}</span>
        </li>
        <li
          className={`pay ${
            !selectedIds || !selectedIds.length ? "disabled" : ""
          }`}
          onClick={handleOnPay}
        >
           
        </li>
      </ul>
    </div>
  );
}

export default React.memo(MyCart);
