import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import * as api from "../../api";
import { Icon } from "antd";
import { setCart } from "../../actions";

function ProductInfo(props) {
  const [tab, setTab] = useState("main");
  const [count, setCount] = useState(1);
  const [isAdding, setIsAdding] = useState(false);
  const dispatch = useDispatch();
  const { product, history } = props;
  const user = useSelector(state => state.user);
  const cart = useSelector(state => state.cart);

  function handleMouseEnter(curTab) {
    curTab !== tab && setTab({ tab: curTab });
  }

  function handleSize(node) {
    if (node) {
      const { width } = node.getBoundingClientRect();
      node.style.height = width + "px";
    }
  }

  function handleCountChange(e) {
    const curCount = parseInt(e.target.value);
    if (curCount && curCount > 0) {
      setCount(curCount);
    }
  }

  function handleBtn(n) {
    if (count + n > 0) {
      setCount(count + n);
    }
  }

  function handleAddToCart(link) {
    if (!user) {
      history.push("/user");
      return;
    }
    if (isAdding) {
      return;
    }
    const { id } = product;
    let products;
    if (cart && cart.product) {
      products = cart.products;
    } else {
      products = [];
    }
    const item = products.find(item => item.id === id);
    if (item) {
      if (count > item.count) {
        setIsAdding(true);
        api
          .addToCart({ id, count, type: "changeCount" })
          .then(res => {
            dispatch(setCart(res.data));
            setIsAdding(false);
            if (link) {
              history.push("/cart");
            }
          })
          .catch(() => {
            setIsAdding(false);
          });
      } else {
        if (link) {
          history.push("/cart");
        }
        return;
      }
    } else {
      setIsAdding(true);
      api
        .addToCart({ id, count })
        .then(res => {
          dispatch(setCart(res.data));
          setIsAdding(false);
          if (link) {
            history.push("/cart");
          }
        })
        .catch(() => {
          setIsAdding(false);
        });
    }
  }

  function handleBuy() {
    handleAddToCart(true);
  }

  return (
    <>
      <div className="tags-bar">
        <div className="tag" onClick={() => history.push("/")}>
          
        </div>
        <Icon type="right" />
        <div className="tag" onClick={() => history.push("/")}>
          
        </div>
        <Icon type="right" />
        {product ? (
          product.category === "male" ? (
            <div className="tag" onClick={() => history.push("/")}>
              
            </div>
          ) : (
            <div className="tag" onClick={() => history.push("/")}>
              
            </div>
          )
        ) : (
          ""
        )}
        <Icon type="right" />
        <div className="tag">{product && product.name}</div>
      </div>
      <div className="content">
        <div className="imgs">
          <div className="show-img" ref={node => handleSize(node)}>
            {tab === "main" ? (
              <img src={product && product.main_image} alt="main" />
            ) : (
              <img src={product && product.sub_image} alt="sub" />
            )}
          </div>
          <div className="focus-img">
            <div
              className={`cover ${tab === "main" ? "active" : ""}`}
              onMouseEnter={() => handleMouseEnter("main")}
            >
              <img src={product && product.main_image} alt="main" />
            </div>
            <div
              className={`feature ${tab === "sub" ? "active" : ""}`}
              onMouseEnter={() => handleMouseEnter("sub")}
            >
              <img src={product && product.sub_image} alt="sub" />
            </div>
          </div>
        </div>
        <div className="goods-wrapper">
          <div className="goods-info">
            <div className="title">
              <h2 className="main-title">{product && product.title}</h2>
              <h4 className="sub-title">  </h4>
            </div>
            <div className="goods-price">
              {product ? (
                product.discount < product.price ? (
                  <>
                    <div className="origin-price">
                      <span className="label w3"></span>
                      <span>{product && product.price.toFixed(2)}</span>
                    </div>
                    <div className="price">
                      <span className="label w3"></span>
                      <span></span>
                      <span className="number">
                        {product && product.discount}
                      </span>
                      <span>.00</span>
                    </div>
                  </>
                ) : (
                  <div className="price">
                    <span className="label w2"></span>
                    <span></span>
                    <span className="number">
                      {product && product.discount}
                    </span>
                    <span>.00</span>
                  </div>
                )
              ) : (
                ""
              )}
              <div className="ems">
                <span className="label w2"></span>
                <span>EMS</span>
                <span className="number"> 0.00 </span>
                <span></span>
              </div>
            </div>
            <div className="count">
              <div className="label w2"></div>
              <span
                className={`minus ${count === 1 ? "disabled" : ""}`}
                onClick={() => handleBtn(-1)}
              >
                -
              </span>
              <input type="text" value={count} onChange={handleCountChange} />
              <span className="plus" onClick={() => handleBtn(1)}>
                +
              </span>
              <span className="remain"></span>
            </div>
            <div className="service">
              <div className="label w2"></div>
              <span></span>
            </div>
          </div>
          <div className="actions">
            <div className="pay" role="button" onClick={handleBuy}>
              
            </div>
            <div
              className="add-to-cart"
              role="button"
              onClick={handleAddToCart}
            >
              
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default React.memo(ProductInfo);
